export default {
	data() {
		return {
			isEmpty:false,
			hasFlag:true,
			page:0,
		};
	},
	methods:{
		// 获取我的收藏
		async getMyCollect(){
			if(!this.hasFlag) return
			this.page = ++this.page;
			let res = await this.$fetch(this.$api.getFocus,{type:1,pageNum:this.page,pageSize:10},'GET')
			console.log(res);
			if(res.total){
				this.isEmpty = false
			}else{
				this.isEmpty = true
			}
			this.hasFlag  = 10*this.page < res.total
		}
	},
	onLoad() {
		this.getMyCollect()
		// setTimeout(()=>{
		// 	this.isEmpty = true
		// },2000)
	}
}
